        p.mb-5 Los textiles son la base fundamental para el desarrollo del proceso productivo en la confección de la falda, estos dan la pauta para empezar a hacer la planeación del proceso, pues dependiendo de sus características se realiza el trazo, se corta el material, se escogen los insumos y se hace el alistamiento de la maquinaria. 
